import React, { Component } from 'react'
import { toyService } from '../services/toyService'
import { Link } from 'react-router-dom';
import { ReviewAdd } from '../cmps/ReviewAdd'
import { loadReviews } from '../store/actions/reviewActions';
import { connect } from 'react-redux';
import { ReviewList } from '../cmps/ReviewList';


class _ToyDetails extends Component {
    state = {
        toy: null
    }

    componentDidMount() {
        this.loadToy()
        this.loadReviews()
    }

    loadReviews() {
        const id = this.props.match.params.toyId
        this.props.loadReviews({ toyId: id })
    }

    loadToy() {
        const id = this.props.match.params.toyId
        toyService.getById(id)
            .then(toy => {
                this.setState({ toy })
            })
    }





    render() {
        const { toy } = this.state
        if (!toy) return <div>Loading...</div>
        return (
            <div>
                <img src={toy.imgUrl ? toy.imgUrl : require(`../assets/img/default.jpg`)} alt="" />
                <h1>name: {toy.name}</h1>
                <h1>price {toy.price}</h1>
                <h1>type: {toy.type}</h1>
                <h1>in stock? {toy.inStock ? 'yes' : 'no'}</h1>
                <ReviewList reviews={this.props.reviews} />
                <ReviewAdd toyId={toy._id} />
                <Link to="/toy" >Back to list</Link>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        reviews: state.reviewReducer.reviews
    }
}

const mapDispatchToProps = {
    loadReviews
}

export const ToyDetails = connect(mapStateToProps, mapDispatchToProps)(_ToyDetails)

