import React, { Component } from 'react'
import { Select, MenuItem, InputLabel } from '@material-ui/core'

export class ToyFilter extends Component {
    state = {
        filter: {
            name: '',
            inStock: false,
            type: '',
            sortBy: ''
        }
    }

    handleChange = (ev) => {
        ev.persist()
        // if (ev.target.name === 'sort') {
        //     this.setState(({ sortBy: ev.target.value }), () => {
        //         this.props.onSetFilter(this.state.sortBy)
        //     })
        // }
        // else {
            const value = (ev.target.type === 'checkbox') ? ev.target.checked : ev.target.value
            this.setState(prevState => ({ filter: { ...prevState.filter, [ev.target.name]: value } }), () => {
                this.props.onSetFilter(this.state.filter)

            })
        // }
    }
    onFilter = (ev) => {
        ev.preventDefault()
        this.props.onSetFilter(this.state.filter)
    }

    render() {
        const { name } = this.state.filter
        return (
            <form onSubmit={this.onFilter} className="form-filter flex space-evenely flex-1">
                <label htmlFor="">By name</label>
                <input type="text" name="name" value={name} onChange={this.handleChange} />
                <label htmlFor="">only in stock</label>
                <input type="checkbox" name="inStock" onChange={this.handleChange} />
                <Select name="type" id="select" onChange={this.handleChange}>
                    <MenuItem value="" >All</MenuItem>
                    <MenuItem value="Educational">Educational</MenuItem>
                    <MenuItem value="Funny">Funny</MenuItem>
                    <MenuItem value="Adult">Adult</MenuItem>
                </Select>
                <select name="sort" id="" onChange={this.handleChange}>
                    <option value="">All</option>
                    <option value="name">name</option>
                    <option value="price">price</option>
                </select>
                <button>Filter</button>
            </form>
        )
    }
}
