
import React from 'react';
import { connect } from 'react-redux';
import { login } from '../store/actions/userActions';

export class _Login extends React.Component {

    state = {
        user: {
            username: '',
            password:''
        }
    }

    onChange = (ev) => {
        console.log(ev);
        this.setState({ user: {...this.state.user, [ev.target.name]: ev.target.value }})
    }

    onLogin = (ev) => {
        ev.preventDefault()
        console.log('hi');
        this.props.login(this.state.user)
            .then(() => this.props.history.push('/toy'))
    }


    render() {
        return (
            <div className="flex column">
                <form className="flex space-between align-center column" onSubmit={this.onLogin}>
                    <input type="text" name="username" onChange={this.onChange} placeholder="Your user name" />
                    <input type="password" name="password" onChange={this.onChange} placeholder="password"/>
                    <button className="submit">submit</button>
                </form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        loggedinUser: state.userReducer.loggedinUser
    }
}

const mapDispatchToProps = {
    login
}

export const Login = connect(mapStateToProps, mapDispatchToProps)(_Login)




